body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #76babd;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container img{
  max-width: 50%;
}


.main{
  background-color: #f7e4c3;
  border-radius: 10px;
  border: 5px solid #ffffff;
  padding: 0.5rem;
  text-align: center;
  color: #000000;
}

.boton-buscar{
  background-color: #a2ffc1;
  border-radius: 15px;
  padding: 10px 25px;
  border-color: aqua;
  cursor: pointer;
  text-transform: uppercase;
  font-family: 'Itim', cursive;
  font-size: 1.2em;
  margin: 1rem 0;
  transition: all 0.5s ease-in-out;
}

.boton-buscar:hover{
  background-color: #1afc66;
  border-color: #000000;
}

.personaje img{
  max-width: 100%;
  border-radius: 5px;
}

